  > .p-galleria-preview-nav-button {
  top: 50%;
  height: 20em;
  width: 4em;
  margin-top: -10em;
}

.FileUploader {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.Card {
  background-color: white;
  margin: 5px;
  padding: 5px;
  width: 90%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}

.Dropzone {
  height: 150px;
  width: 150px;
  background-color: #fff;
  border: 2px dashed rgb(187, 186, 186);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 16px;
}

.Highlight {
  background-color: rgb(188, 185, 236);
}

.Icon {
  opacity: 0.3;
  height: 64px;
  width: 64px;
}

.FileInput {
  display: none;
}

.Upload {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  align-items: flex-start;
  text-align: left;
  overflow: hidden;
}

.Content {
  display: flex;
  flex-direction: row;
  padding-top: 16px;
  box-sizing: border-box;
  width: 100%;
}

.Files {
  margin-left: 32px;
  align-items: flex-start;
  justify-items: flex-start;
  flex: 1 1;
  overflow-y: auto;
  max-height: 150px;
}

.Actions {
  display: flex;
  flex: 1 1;
  width: 100%;
  align-items: flex-end;
  flex-direction: column;
  margin-top: 5px;
}

.Title {
  margin-bottom: 32px;
  color: #555;
}

.Filename {
  margin-bottom: 8px;
  font-size: 1vw;
  color: #555;
}

.Row {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
  box-sizing: border-box;
}

.CheckIcon {
  opacity: 0.5;
  margin-left: 32px;
}

.ProgressWrapper {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  align-items: center;
}

.Button {
  display: inline-block;
  height: 36px;
  min-width: 88px;
  padding: 6px 16px;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 0;
  border-radius: 2px;
  color: #000;
  margin: 1px;
  outline: 0;
}

button:disabled {
  background: rgb(189, 189, 189);
  cursor: default;
}

html,
body,
#root {
  height: 100% !important;
  width: 100%;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

body {
  color: #666;
  font-size: 62.5%;
}

p,
a,
div {
  font-size: 16px;
}

a {
  color: #666;
  text-decoration: none;
}

a svg-inline--fa {
  margin-right: 5px;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

ul li {
  padding: 0;
  margin: 0;
}

input[type='text'] {
  box-sizing: border-box;
  border: 1px solid hsl(0, 0%, 80%);
  border-radius: 4px;
  width: 100%;
  padding: 7px 10px;
}

/**
Temporary fix to overwrite and display the default style of the checkbox
to prevent issues with the custom component and react/fontawesome missing icons
 */
input[type="checkbox"] {
  appearance: checkbox !important;
  -webkit-appearance: checkbox !important;
  -moz-appearance: checkbox !important;
  margin: 0 0 0 16px !important
}
input[type="checkbox"]::before {
  content: none !important;
}
/**
Ends temporary react/fontawesome fix
 */

.link,
.link-button {
  color: #449df5;
  font-family: Raleway;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.75px;
  line-height: 14px;
  text-align: center;
  text-decoration: none;
  padding: 0;
}

.link:active,
.link-button:active,
.link:hover,
.link-button:hover {
  text-decoration: none;
}

* {
  font-family: 'Raleway', sans-serif;
}

.form-group {
  text-align: left;
  margin: 12px 0;
}

.form-group:first-child {
  margin-top: 0;
}

.form-group > label {
  color: #5361fd;
  font-family: Raleway;
  font-size: 11px;
  font-weight: bold;
  letter-spacing: 0.75px;
  line-height: 14px;
  text-transform: uppercase;
  display: block;
}

.form-group > input {
  height: 38px;
  width: 100%;
  margin: 10px 0;
  border-radius: 4px;
  border: solid 1px #ccc;
  box-sizing: border-box;
}

form-group .select-role {
  margin: 10px 0;
}

/* Overriding the center-align property from the styleguide */
.solid-provider-select {
  text-align: left !important;
}

.solid-provider-select .option {
  padding-left: 10px !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

input[type="number"], textarea {
  border: 1px solid #DAE0E6;
  font-size: 0.8em;
  padding: 0.5em 0;
  text-indent: 0.75em;
  border-radius: 4px;
